import React from "react";
import axios from "axios";
import Movie from "./movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  };
  //getMovies 함수는 우리에게 영화 데이터를 제공한다.
  //사용하기 위해서 각 데이터를 가져와야ㅐ 한다.
  //getMovies 함수의 역할 = 데이터 사용화!
  //함수에 async를 한 이유는 비동기화 시키고 싶기 때문이다. 시간이 꽤 거리는 작업이 있을 때 사용!
  //이를 위해서는 async 하고자 하는 상수에도 마찬가지로 await을 앞에 넣어야 한다.
  getMovies = async () => {
    const {
      data: {
        data: { movies }
      }
    } = await axios.get(
      "https://yts-proxy.now.sh/list_movies.json?sort_by=rating"
    );
    this.setState({ movies, isLoading: false });
  };
  //컴포넌트 딧 함수는 render직후에 실행된다.
  componentDidMount() {
    this.getMovies();
  }

  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <div className="loader loader-2"></div>
          </div>
        ) : (
          <div className="movies">
            {movies.map(movie => {
              return (
                <Movie
                  key={movie.id}
                  id={movie.id}
                  year={movie.year}
                  title={movie.title}
                  summary={movie.summary}
                  poster={movie.medium_cover_image}
                  runtime={movie.runtime}
                  genres={movie.genres}
                />
              );
            })}
          </div>
        )}
      </section>
    );
  }
}

export default App;
