import React from "react";
import PropTypes from "prop-types";
import "./movie.css";

function Movie({ year, title, summary, poster, runtime, genres }) {
  return (
    <main className="movies_movie">
      <div className="movie_data">
        <img id="image" src={poster} alt={title} title={title}></img>
        {/*alt는 블라인드를 위한 것! */}
        <div className="etc">
          <h3 className="move_title">{title}</h3>

          <div className="wrap">
            <h5 className="movie_year">{year}</h5>
            <p className="movie_runtime">{runtime}m</p>
          </div>

          <ul className="genres">
            {genres.map((genre, index) => (
              <li key={index} className="genre_genres">
                {genre}
              </li>
            ))}
          </ul>
          <p className="movie_summary">{summary.slice(0, 200)}</p>
        </div>
      </div>
    </main>
  );
}
//PropTypes 체킹을 위해서 프롭 타입 체크용 프롭 타입을 만들어 준다!
// propTypes 첫글자 대문자 아님!!!!!!!!!!!!!!!!
Movie.propTypes = {
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  year: PropTypes.number.isRequired,
  runtime: PropTypes.number.isRequired
};

export default Movie;
